import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Card from './Cards';
import Sdata from './Sdata';
import HeadTitle from './HeadTitle';


ReactDOM.render(
  <>
    <HeadTitle />
    <div className="Cards__Jsx">
      {Sdata.map((val) => {
        return (
          <Card
            img={val.img}
            title={val.title}
            pName={val.pName}
          />
        );
      })}
    </div>
  </>,
  document.getElementById('root')
);

