function Card(props) {
    console.log(props);
    return (
        <>
            <div className="cards">
                <div className="card">
                    <img src={props.img} style={{ width: "100%" }} alt="" />
                    <div className="card__info">
                        <h1><span className="Underline">{props.title}</span></h1>
                        <p>{props.pName}</p>
                        <div className="btn">
                            <button>Check Here</button>
                        </div>
                    </div>
                </div>
                <svg id="Svg__Wave" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#423F8D" fill-opacity="1" d="M0,128L120,154.7C240,181,480,235,720,245.3C960,256,1200,224,1320,208L1440,192L1440,320L1320,320C1200,320,960,320,720,320C480,320,240,320,120,320L0,320Z"></path></svg>
            </div>
        </>
    );
}

export default Card;